<script>
    import page from 'page';
	import Contact from "./pages/contact.svelte";
	import About from "./pages/about.svelte";
    import Home from "./pages/home.svelte";
	import NotFound from "./pages/notFound.svelte";

    let currentPage,params;

    const routes = {
        Home,
        About,
        Contact,
        NotFound
    };

    page('/*', setParams, setPage);

    function setParams(context, next){

        params = context.params;

        debugger;

        next();
    }

    function setPage(context, next){

        debugger;

        const path = capitalize(context.path === '/' ? 'home' : context.path)
        debugger;
        const nextPage = routes[path] || routes.NotFound;

        debugger;

        currentPage = nextPage;
    }

    function capitalize(str){
        return str.charAt(0).toUpperCase() + str.slice(1);
    }

    page.start();
</script>

<div class="app-container">
	<header>
		i am the header
	</header>

    {#each Object.keys(routes) as routeName}
        <button on:click={() => page(routeName)}>{`go to ${routeName}`}</button>
    {/each}

	<main>
        <svelte:component this={currentPage} params={params} />
    </main>
</div>

